<!-- 添加考试 -->
<template>
  <section class="add">
    <el-form ref="form" :model="form" :rules="rules2" label-width="80px">
      <el-form-item label="考试科目" prop="source">
        <el-input v-model="form.source" placeholder="请输入考试科目"></el-input>
      </el-form-item>
      <!-- <el-form-item label="描述">
        <el-input
          v-model="form.description"
          placeholder="请输入试卷描述"
        ></el-input>
      </el-form-item> -->
      <el-form-item label="所属专业" prop="major">
        <el-select v-model="form.major" placeholder="请选择本次考试所属专业">
          <el-option
            label="计算机科学与技术"
            value="计算机科学与技术"
          ></el-option>
          <el-option label="网络工程" value="网络工程"></el-option>
          <el-option label="软件工程" value="软件工程"></el-option>
          <el-option label="信息工程" value="信息工程"></el-option>
        </el-select>
      </el-form-item>
      <!-- <el-form-item label="所属学院">
        <el-input v-model="form.institute"></el-input>
      </el-form-item> -->
      <el-form-item label="年级" prop="grade">
        <el-select v-model="form.grade" placeholder="请选择本次考试所属年级">
          <el-option label="2017" value="2017"></el-option>
          <el-option label="2018" value="2018"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="考试日期" prop="examDate">
        <el-col :span="11">
          <el-date-picker
            placeholder="选择日期"
            v-model="form.examDate"
            style="width: 100%;"
          ></el-date-picker>
        </el-col>
      </el-form-item>
      <el-form-item label="考试时长" prop="totalTime">
        <el-input
          v-model="form.totalTime"
          placeholder="请输入考试时长"
        ></el-input>
      </el-form-item>
      <!-- <el-form-item label="总分">
        <el-input v-model="form.totalScore"></el-input>
      </el-form-item> -->
      <el-form-item label="考试描述" prop="type">
        <el-input
          v-model="form.type"
          placeholder="请输入考试描述 (如：2021年上学期期末考试)"
        ></el-input>
      </el-form-item>
      <el-form-item label="考生须知">
        <el-input
          type="textarea"
          v-model="form.tips"
          placeholder="请输入考生须知"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit('form')">立即创建</el-button>
        <el-button type="text" @click="cancel()">取消</el-button>
      </el-form-item>
    </el-form>
  </section>
</template>

<script>
export default {
  data() {
    return {
      form: {
        //表单数据初始化
        source: null,
        description: null,
        institute: "计算机学院",
        major: null,
        grade: null,
        examDate: null,
        totalTime: null,
        totalScore: 100,
        type: null,
        tips: null,
        paperId: null
      },
      rules2: {
        source: [
          { required: true, message: "考试科目不能为空", trigger: "blur" }
        ],
        major: [
          { required: true, message: "请选择所属专业", trigger: "change" }
        ],
        grade: [{ required: true, message: "请选择年级", trigger: "change" }],
        examDate: [
          { required: true, message: "请选择考试日期", trigger: "change" }
        ],
        totalTime: [
          { required: true, message: "考试时长不能为空", trigger: "blur" }
        ],
        type: [{ required: true, message: "考试描述不能为空", trigger: "blur" }]
      }
    };
  },
  methods: {
    formatTime(date) {
      //日期格式化
      let year = date.getFullYear();
      let month =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      let day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      let hours =
        date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
      let minutes =
        date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
      let seconds =
        date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
      // 拼接
      return (
        year +
        "-" +
        month +
        "-" +
        day +
        " " +
        hours +
        ":" +
        minutes +
        ":" +
        seconds
      );
    },
    onSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let examDate = this.formatTime(this.form.examDate);
          this.form.examDate = examDate.substr(0, 10);
          this.$axios(`/api/examManagePaperId`).then(res => {
            this.form.paperId = res.data.data.paperId + 1; //实现paperId自增1
            this.$axios({
              url: "/api/exam",
              method: "post",
              data: {
                ...this.form
              }
            }).then(res => {
              if (res.data.code == 200) {
                this.$message({
                  message: "考试添加成功",
                  type: "success"
                });
                this.$router.push({ path: "/selectExam" });
              }
            });
          });
        } else {
          return false;
        }
      });
    },
    cancel() {
      //取消按钮
      this.form = {};
    }
  }
};
</script>
<style lang="scss" scoped>
.add {
  padding: 0px 40px;
  width: 400px;
}
</style>



